"use client";

import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Draggable from 'gsap/Draggable';
import './CardSwap.css'
gsap.registerPlugin(ScrollTrigger, Draggable);

const COVERS = [
  "./logo_sc.png",
  "https://i.scdn.co/image/ab67616d00001e02d9194aa18fa4c9362b47464f",
  "https://i.scdn.co/image/ab67616d00001e02a7ea08ab3914c5fb2084a8ac",
  "https://i.scdn.co/image/ab67616d00001e0213ca80c3035333e5a6fcea59",
  "https://i.scdn.co/image/ab67616d00001e02df04e6071763615d44643725",
  "https://i.scdn.co/image/ab67616d00001e0239c7302c04f8d06f60e14403",
  "https://i.scdn.co/image/ab67616d00001e021c0bcf8b536295438d26c70d",
  "https://i.scdn.co/image/ab67616d00001e029bbd79106e510d13a9a5ec33",
  "https://i.scdn.co/image/ab67616d00001e021d97ca7376f835055f828139",
  "https://www.udiscovermusic.com/wp-content/uploads/2015/10/Kanye-West-Yeezus.jpg",
];

const AlbumCarousel: React.FC = () => {
  const boxesRef = useRef<HTMLDivElement>(null);
  const nextRef = useRef<HTMLButtonElement>(null);
  const prevRef = useRef<HTMLButtonElement>(null);

  useEffect(() => {
    const BOXES = gsap.utils.toArray<HTMLElement>('.box');
    gsap.set('.box', { display: 'block', yPercent: -50 });
    gsap.set('button', { z: 200 });

    const STAGGER = 0.1;
    const DURATION = 1;
    const OFFSET = 0;
    const LOOP = gsap.timeline({ paused: true, repeat: -1, ease: 'none' });
    const SHIFTS = [...BOXES, ...BOXES, ...BOXES];

    SHIFTS.forEach((BOX, index) => {
      const BOX_TL = gsap.timeline()
        .set(BOX, { xPercent: 250, rotateY: -50, opacity: 0, scale: 0.5 })
        .to(BOX, { opacity: 1, scale: 1, duration: 0.1 }, 0)
        .to(BOX, { opacity: 0, scale: 0.5, duration: 0.1 }, 0.9)
        .fromTo(BOX, { xPercent: 250 }, { xPercent: -350, duration: 1, ease: 'power1.inOut', immediateRender: false }, 0)
        .fromTo(BOX, { rotateY: -50 }, { rotateY: 50, duration: 1, ease: 'power4.inOut', immediateRender: false }, 0)
        .to(BOX, { z: 100, scale: 1.25, duration: 0.1, repeat: 1, yoyo: true }, 0.4)
        .fromTo(BOX, { zIndex: 1 }, { zIndex: BOXES.length, repeat: 1, yoyo: true, ease: 'none', duration: 0.5, immediateRender: false }, 0);
      LOOP.add(BOX_TL, index * STAGGER);
    });

    const CYCLE_DURATION = STAGGER * BOXES.length;
    const START_TIME = CYCLE_DURATION + DURATION * 0.5 + OFFSET;

    const LOOP_HEAD = gsap.fromTo(
      LOOP,
      { totalTime: START_TIME },
      { totalTime: `+=${CYCLE_DURATION}`, duration: 1, ease: 'none', repeat: -1, paused: true }
    );

    const PLAYHEAD = { position: 0 };
    const POSITION_WRAP = gsap.utils.wrap(0, LOOP_HEAD.duration());

    const SCRUB = gsap.to(PLAYHEAD, {
      position: 0,
      onUpdate: () => {
        LOOP_HEAD.totalTime(POSITION_WRAP(PLAYHEAD.position));
      },
      paused: true,
      duration: 0.25,
      ease: 'power3',
    });

    let iteration = 0;

    const TRIGGER = ScrollTrigger.create({
      start: 0,
      end: '+=2000',
      pin: '.boxes',
      onUpdate: self => {
        const SCROLL = self.scroll();
        if (SCROLL > self.end - 1) {
          WRAP(1, 1);
        } else if (SCROLL < 1 && self.direction < 0) {
          WRAP(-1, self.end - 1);
        } else {
          const NEW_POS = (iteration + self.progress) * LOOP_HEAD.duration();
          SCRUB.vars.position = NEW_POS;
          SCRUB.invalidate().restart();
        }
      },
    });

    const WRAP = (iterationDelta: number, scrollTo: number) => {
      iteration += iterationDelta;
      TRIGGER.scroll(scrollTo);
      TRIGGER.update();
    };

    const SNAP = gsap.utils.snap(1 / BOXES.length);

    const progressToScroll = (progress: number) =>
      gsap.utils.clamp(1, TRIGGER.end - 1, gsap.utils.wrap(0, 1, progress) * TRIGGER.end);

    const scrollToPosition = (position: number) => {
      const SNAP_POS = SNAP(position);
      const PROGRESS = (SNAP_POS - LOOP_HEAD.duration() * iteration) / LOOP_HEAD.duration();
      const SCROLL = progressToScroll(PROGRESS);
      if (PROGRESS >= 1 || PROGRESS < 0) return WRAP(Math.floor(PROGRESS), SCROLL);
      TRIGGER.scroll(SCROLL);
    };

    const NEXT = () => scrollToPosition(SCRUB.vars.position - 1 / BOXES.length);
    const PREV = () => scrollToPosition(SCRUB.vars.position + 1 / BOXES.length);

    document.addEventListener('keydown', event => {
      if (event.code === 'ArrowLeft' || event.code === 'KeyA') NEXT();
      if (event.code === 'ArrowRight' || event.code === 'KeyD') PREV();
    });

    const boxContainer = boxesRef.current;
    boxContainer?.addEventListener('click', e => {
      const BOX = (e.target as HTMLElement).closest('.box') as HTMLElement;
      if (BOX) {
        let TARGET = BOXES.indexOf(BOX);
        let CURRENT = gsap.utils.wrap(0, BOXES.length, Math.floor(BOXES.length * SCRUB.vars.position));
        let BUMP = TARGET - CURRENT;
        if (TARGET > CURRENT && TARGET - CURRENT > BOXES.length * 0.5) BUMP = (BOXES.length - BUMP) * -1;
        if (CURRENT > TARGET && CURRENT - TARGET > BOXES.length * 0.5) BUMP = BOXES.length + BUMP;
        scrollToPosition(SCRUB.vars.position + BUMP * (1 / BOXES.length));
      }
    });

    Draggable.create('.drag-proxy', {
      type: 'x',
      trigger: '.box',
      onPress() {
        this.startOffset = SCRUB.vars.position;
      },
      onDrag() {
        SCRUB.vars.position = this.startOffset + (this.startX - this.x) * 0.001;
        SCRUB.invalidate().restart();
      },
      onDragEnd() {
        scrollToPosition(SCRUB.vars.position);
      },
    });

    nextRef.current?.addEventListener('click', NEXT);
    prevRef.current?.addEventListener('click', PREV);

    return () => {
      ScrollTrigger.getAll().forEach(st => st.kill());
    };
  }, []);

  return (
    <div className="boxes" ref={boxesRef} style={{ height: '100vh', width: '100%', position: 'absolute', perspective: 800 }}>
      {COVERS.map((cover, index) => (
        <div
          key={index}
          className="box"
          style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            height: '20vmin',
            width: '20vmin',
            minHeight: '200px',
            minWidth: '200px',
            display: 'none',
            transformStyle: 'preserve-3d',
          }}
        >
          <img src={cover} alt={`Album ${index + 1}`} style={{ width: '100%', height: '100%', objectFit: 'cover', position: 'absolute', top: 0, left: 0 }} />
        </div>
      ))}

      <div className="controls" style={{ position: 'absolute', top: 'calc(50% + 20vmin)', left: '50%', transform: 'translate(-50%, -50%) scale(1.5)', display: 'flex', justifyContent: 'space-between', minWidth: '200px', height: '44px', zIndex: 300 }}>
        <button ref={nextRef} aria-label="Previous album"></button>
        <button ref={prevRef} aria-label="Next album"></button>
      </div>

      <div className="drag-proxy" style={{ position: 'absolute', visibility: 'hidden' }}></div>
    </div>
  );
};

export default AlbumCarousel;
